@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed {
		@include scroll-blocked(); } }

body {
	position: relative;
	font-family: $font-norm;
	@include font-light;
	@include font($font-size, $line-height);
	color: $text-color;
	min-width: $page-layout-min;
	//background: $bg-color url(../../img/empty.png) no-repeat left top fixed
	background: $bg-color url(../../img/bg-page.png) center center;

	@include media('<tab-lg') {
		@include font($font-size--mob, $line-height--mob); }

	// выделение текста
	//*::selection
	//ackground-color: white
 }	//olor: $base-color

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	@include font-light;
	padding: 0;
	color: $title-color;
	margin: 0 0 $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; }

	a {
		text-decoration: none;
		color: inherit;

		&:hover {
			text-decoration: underline; } } }

h1, .h1 {
	@include font-h1; }

h2, .h2 {
	@include font-h2; }

h3, .h3 {
	@include font-h3; }

h4, .h4 {
	margin-bottom: $bottom-space;
	@include font-h4;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--mob; } }

h5, .h5 {
	@include font($font-size-h5, $line-height-h5); }

h6, .h6 {
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

p {
	padding: 0;
	margin: 0 0 $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--mob; }

	&:last-child {
		margin-bottom: 0; } }

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--mob; } }

a {
	color: $link-color;
	text-decoration: none;
	cursor: default;
	// transition
	transition: color $animation-speed ease-in-out;

	.touchevents & {
		outline: none;
		-webkit-tap-highlight-color: transparent; }

	&:link {
		cursor: pointer; }

	//&:not([class*="btn-"]):not([href*="javascript:;"]):hover
	&:hover {
		text-decoration: none;
		color: $link-color--hover; }

	//&[href^="tel"], .no-touchevents &[href^="tel"]:hover
	&[href^="tel:"] {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

b, strong, th {
	@include font-medium; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

.highlight, .mark {
	color: $highlight-color; }

.text-color {
	color: $text-color; }

.text-color-light {
	color: $text-color-light; }

.text-color-alt {
	color: $base-color-alt; }

.text-color-dark {
	color: $base-color-dark; }

.align-center {
	text-align: center; }

.align-left {
	text-align: left; }

.align-right {
	text-align: right; }

.float-left {
	float: left; }

.float-right {
	float: right; }

.nobr {
	white-space: nowrap; }

.hide-text {
	@include hide-text; }

.hide {
	display: none !important; }

.hide-desk {
	@include media('>=desk-lg') {
		display: none !important; } }

.show-desk {
	@include media('<desk-lg') {
		display: none !important; } }

.section, .bottom-space-section, .#{$user-content} .bottom-space-section {
	margin-bottom: $bottom-space--section;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--section--mob; } }

.article, .bottom-space-article, .#{$user-content} .bottom-space-article {
	margin-bottom: $bottom-space--article;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--article--mob; } }

.bottom-space-element, .#{$user-content} .bottom-space-element {
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; } }

.bottom-space-row, .#{$user-content} .bottom-space-row {
	margin-bottom: $bottom-space--row;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--row--mob; } }

.enter-text {
	@include font-normal;
	@include font(2.2rem, round-float(26/22, 2));
	color: rgba($highlight-color, 0.8);
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		font-size: 1.6rem;
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; } }

