/* list */
.#{$user-content} {
	//list
	ul, ol {
		margin: $bottom-space--element 0;
		padding: 0;
		list-style: none;

		@include media('<tab-lg') {
			@include margin-vertical($bottom-space--element--mob); }

		li {
			padding-left: $list-space;
			margin: 0 0 $bottom-space;
			position: relative;

			@include media('<tab-lg') {
				margin-bottom: $bottom-space--mob; } } }

	ul > li::before {
		content: '';
		display: block;
		float: left;
		@include size(12px);
		margin: 0.35em 0 -0.35em -#{$list-space};
		border-radius: 6px;
		background-color: $highlight-color; }

	ol {
		counter-reset: section;

		& > li {
			$size: 32px;
			$space: 53px;
			$size--mob: 20px;
			$space--mob: 35px;
			//===================
			counter-increment: section;
			height: 1%;
			//overflow: hidden
			padding-left: $space;

			@include media('<tab-lg') {
				padding-left: $space--mob; }

			&::before {
				position: relative;
				display: inline-block;
				content: counter(section);
				@include size($size);
				margin: 0 calc(#{$space} - #{$size}) 0 -#{$space};
				@include font-bold;
				line-height: $size;
				text-align: center;
				color: $highlight-color;
				background-color: white;
				border-radius: round($size / 2);

				@include media('<tab-lg') {
					@include size($size--mob);
					margin: 0 calc(#{$space--mob} - #{$size--mob}) 0 -#{$space--mob};
					line-height: $size--mob;
					border-radius: round($size--mob / 2); } } } }

	ul, ol {
		ul, ol {
			margin: round($bottom-space*0.5) 0;

			@include media('<tab-lg') {
				@include margin-vertical(round($bottom-space--mob*0.5)); } } } }

/* list reset */
.reset-list,
.#{$user-content} .reset-list {
	list-style: none;

	li {
		&, &:first-child {
			padding: 0;

			&::before {
				content: none; } } } }

/* list reset end */
