/* blockquote */
blockquote {
	display: block;
	position: relative;
	width: 469px;
	margin: $bottom-space--element auto;
	padding: 0;
	//background-color: lightcoral

	@include media('<tab-lg') {
		margin: $bottom-space--element--mob auto; }

	q {
		display: block;
		position: relative;
		margin: 0;
		padding: 9rem 0;
		font-family: inherit;
		@include font-medium;
		@include font(3.0rem, round-float(36/30, 2));
		color: $text-color; }

	cite {
		position: absolute;
		bottom: 5rem;
		left: 0;
		text-align: left;
		display: block;
		margin-left: auto;
		font-family: inherit;
		@include font-italic;
		@include font($font-size, $line-height);
		color: $highlight-color;

		@include media('<tab-lg') {
			@include font($font-size--mob, $line-height--mob); } }

	&, & q {
		&::before, &::after {
			content: "";
			display: block;
			position: absolute;
			left: 50%;
			height: nth($sep-size, 1);
			width: nth($sep-size, 2);
			margin-left: -#{round(nth($sep-size, 2)/2)};
			background-color: $highlight-color;
			transform: rotate($sep-rotate); }

		&::before {
			top: 0;
			margin-top: 14px; }

		&::after {
			bottom: 0;
			margin-bottom: 14px; } }

	q {
		&::before {
			margin-top: 30px; }

		&::after {
			margin-bottom: 30px; } } }

/* blockquote end */

