/* columns */
.cols {
	position: relative;
	@include clear;
	margin: $bottom-space--article 0 $bottom-space--section;

	@include media('<tab-lg') {
		margin: $bottom-space--article--mob 0 $bottom-space--section--mob; }

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 50%;
		top: 8px;
		bottom: 0;
		border-right: 1px solid $text-color;
		opacity: 0.2; }

	.col {
		float: left;
		width: calc(50% - #{nth($page-layout-space, 2)});
		margin: 0 nth($page-layout-space, 2);

		@include media('<desk-lg') {
			width: calc(50% - #{nth($page-layout-space--md, 2)});
			margin: 0 nth($page-layout-space--md, 2); }

		&:nth-child(odd) {
			margin-left: 0; }

		&:nth-child(even) {
			margin-right: 0; } } }

/* columns end */

.row {
	@include clear;

	.col {
		float: left;
		width: 100%; }

	.col-6 {
		width: 50%; } }

/* separator */
.sep {
	position: relative;
	height: 50px;
	@include margin-vertical($bottom-space--element);
	text-indent: -9999px;
	overflow: hidden;

	@include media('<tab-lg') {
		height: 26px;
		@include margin-vertical($bottom-space--element--mob); }

	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		left: 50%;
		height: nth($sep-size, 1);
		width: nth($sep-size, 2);
		margin-left: -#{round(nth($sep-size, 2)/2)};
		background-color: $highlight-color;
		transform: rotate($sep-rotate);
		@include margin-vertical(14px);

		@include media('<tab-lg') {
			height: nth($sep-size--mob, 1);
			width: nth($sep-size--mob, 2);
			margin-left: -#{round(nth($sep-size--mob, 2)/2)};
			@include margin-vertical(7px); } }

	&::before {
		top: 0; }

	&::after {
		bottom: 0; } }
