.user-content ul > li::before {
	content: '';
	display: block;
	float: left;
	height: 12px;
	width: 12px;
	margin: 0.35em 0 -0.35em -34px;
	border-radius: 6px;
	background-color: #6bdbbc;
}

.user-content ol {
	counter-reset: section;
}

