blockquote::before, blockquote::after, blockquote q::before, blockquote q::after {
	content: "";
	display: block;
	position: absolute;
	left: 50%;
	height: 5px;
	width: 46px;
	margin-left: -23px;
	background-color: #6bdbbc;
	transform: rotate(-36deg);
}

blockquote::before, blockquote q::before {
	top: 0;
	margin-top: 14px;
}

blockquote::after, blockquote q::after {
	bottom: 0;
	margin-bottom: 14px;
}

