	left: 50%;
	top: 8px;
	bottom: 0;
	border-right: 1px solid white;
	opacity: 0.2;
}

.cols .col {
	float: left;
	width: calc(50% - 8rem);
	margin: 0 8rem;
}

@media (max-width: 1899px) {
	.cols .col {
