/* === BUTTON === */
[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
}

[class|="btn"] input[type="submit"], [class|="btn"] input[type="button"], [class|="btn"] input[type="reset"], [class*=" btn-"] input[type="submit"], [class*=" btn-"] input[type="button"], [class*=" btn-"] input[type="reset"] {
	background: url("../img/empty.png") repeat;
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	padding: 0;
	border: none;
	z-index: 99;
	opacity: 0;
}

/* btn-default */
[class*="btn-def"] {
	position: relative;
	padding: 19px 46px 19px 32px;
	margin: 0;
	border: none;
	border-radius: 30px;
	background: #6bdbbc;
	box-shadow: none;
	font-family: "Roboto", sans-serif;
	font-weight: normal;
	font-style: normal;
	color: white;
	text-align: left;
	transition-property: color, background-color;
	transition-duration: 0.2s;
}

.touchevents [class*="btn-def"] {
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

[class*="btn-def"]::before, [class*="btn-def"]::after {
	content: "";
	display: block;
	position: absolute;
	right: 32px;
	width: 7px;
	height: 1px;
	margin: 2px -1px;
