}

@keyframes pulse {
	0% {
		transform: scale(1);
		transform: scale3d(1, 1, 1);
	}
	33% {
		transform: scale(1.09);
		transform: scale3d(1.09, 1.09, 1);
	}
	to {
		transform: scale(1);
		transform: scale3d(1, 1, 1);
	}
}

@keyframes pulseArr {
	0% {
		transform: translate(0, 0);
		transform: translateX(0);
	}
	33% {
		transform: translate(1rem, 0);
		transform: translateX(1rem);
	}
	to {
		transform: translate(0, 0);
		transform: translateX(0);
