/* pop */
.pop {
	display: inline-block;
	vertical-align: top;
	position: relative;
	color: inherit;
	cursor: default;
}

.pop > span {
	display: inline-block;
	vertical-align: top;
	position: relative;
	padding: 0 0.7em;
	font-weight: 500;
	white-space: nowrap;
	cursor: pointer;
	transition: color 0.2s ease-in-out;
}

.pop > span::before {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: -0.2em;
	width: 100%;
	height: calc(100% + 0.4em);
	border-radius: 20px;
	border: 2px solid #6bdbbc;
	will-change: transform, opacity;
	transition-property: transform, opacity;
	transition-duration: 0.2s;
	transition-timing-function: ease-in-out;
}

.pop:active > span {
	transform: translate(0, 1px);
}

.pop.pop-is-open > span {
	z-index: 10;
}

@media (min-width: 992px) {
	.pop.pop-is-open > span::before {
		opacity: 0;
	}
}

.no-touchevents .pop > span:hover {
	color: #5eb8b2;
}

@media (min-width: 992px) {
	.no-touchevents .pop.pop-is-open > span:hover {
		color: #333399;
	}
}

.pop__content {
	position: absolute;
	left: 0;
	top: -0.2em;
	width: 440px;
	padding: 5rem 4rem 3rem;
	color: #333399;
	background-color: #6bdbbc;
	border-radius: 15px;
	z-index: 5;
	opacity: 0;
	pointer-events: none;
	transform: translate(0, 2em);
	transform: translate3d(0, 2em, 0);
	will-change: transform, opacity;
	transition-property: transform, opacity;
	transition-duration: 0.6s;
	transition-timing-function: cubic-bezier(0.55, 0.085, 0, 0.99);
}

@media (max-width: 991px) {
	.pop__content {
		position: fixed;
		left: 50%;
		top: 50%;
		padding: 3rem 2rem 1.5rem;
		max-width: calc(100% - 4rem);
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
		z-index: 15;
		transform: translate(-50%, calc(-50% + 2em + 30px));
		transform: translate3d(-50%, calc(-50% + 2em + 30px), 0);
	}
}

.pop-is-open .pop__content {
	opacity: 1;
	pointer-events: auto;
	transform: translate(0, 0);
	transform: translate3d(0, 0, 0);
}

@media (max-width: 991px) {
	.pop-is-open .pop__content {
		transform: translate(-50%, calc(-50% + 30px));
		transform: translate3d(-50%, calc(-50% + 30px), 0);
	}
}

.pop__title {
	display: block;
	margin-bottom: 1em;
}

.pop__close {
	position: absolute;
	top: 8px;
	right: 8px;
	width: 20px;
	height: 20px;
	color: white;
	text-indent: -9999px;
	cursor: pointer;
	overflow: hidden;
	opacity: 0;
	transform: rotate(-0.25turn);
	transform: rotate(-0.25turn) translateZ(0);
	will-change: transform, opacity;
	transition-delay: 0.3s;
	transition-property: transform, opacity;
	transition-duration: 0.6s;
	transition-timing-function: ease-in-out;
}

.pop-is-open .pop__close {
	opacity: 1;
	transform: rotate(0turn);
	transform: rotate(0turn) translateZ(0);
	transition-delay: 0.2s;
}

.pop__close::before, .pop__close::after {
	content: "";
	display: block;
	position: absolute;
	height: 2px;
	width: 20px;
	margin: -1px 0 0 -10px;
	top: 50%;
	left: 50%;
	background-color: currentColor;
	transition: background-color 0.2s ease-in-out;
}

.pop__close::before {
	transform: rotate(0.125turn);
}

.pop__close::after {
	transform: rotate(-0.125turn);
}

.no-touchevents .pop__close:hover::before, .no-touchevents .pop__close:hover::after {
	background-color: #333399;
}

