@import "../utils/functions";
@import "../utils/placeholders";

// ==========================================================================
// Variables
// ==========================================================================

// Medium query
//$breakpoints: ('mob-sm': 320px, 'mob-md': 480px, 'mob-lg': 640px, 'tab-sm': 768px, 'tab-lg': 992px, 'desk-sm': 1200px, 'desk-md': 1366px, 'desk-lg': 1600px, 'desk-hd': 1920px)
$breakpoints: ('desk-qhd': 2560px, 'desk-hd': 1920px, 'desk-lg': 1900px, 'desk-ml': 1440px, 'desk-md': 1366px, 'desk-sm': 1280px, 'tab-lg': 992px, 'tab-sm': 768px, 'mob-lg': 640px, 'mob-md': 480px, 'mob-sm': 320px);

// Fonts style
// ==========================================================================
$font-norm: 'Roboto', sans-serif !default;
$font-alt: 'Playfair Display', serif !default;

// Reset font-weight and font-style
// Use for font-face
@mixin font-reset() {
	font: {
		weight: normal;
		style: normal; } }

// Main font
@mixin font-normal() {
	font-family: $font-norm;
	font-weight: normal;
	font-style: normal; }

@mixin font-bold() {
	font-weight: 700; }

@mixin font-italic() {
	font-weight: 300;
	font-style: italic; }

@mixin font-bold-italic() {
	font-weight: 700;
	font-style: italic; }

@mixin font-medium() {
	font-weight: 500; }

@mixin font-light() {
	font-weight: 300; }

// Alternative font
@mixin font-alt() {
	font-family: $font-alt;
	font-weight: 900; }

@mixin font-alt-black() {
	font-family: $font-alt;
	font-weight: 900; }

@mixin font-alt-bold() {
	font-family: $font-alt; }

@mixin font-alt-bold-italic() {
	font-family: $font-alt; }


// Colors
// ==========================================================================

// Color of brands
$base-color: #333399 !default;
$base-color-alt: #2b2b8b !default;
$base-color-dark: #242473 !default;
$mark-color: #f25984 !default;
$highlight-color: #6bdbbc !default;
$highlight-color--hover: #5eb8b2 !default;
// ----------------------------------------------------
$bg-color: $base-color !default;
$bg-color-alt: white !default;
// ----------------------------------------------------
$text-color: white !default;
$text-color-alt: white !default;
$text-color-light: #9a9ac6 !default;
$title-color: white !default;
// ----------------------------------------------------
$link-color: $highlight-color !default;
$link-color--hover: $highlight-color--hover !default;
// ----------------------------------------------------
$border-color: lightgray !default;
// ----------------------------------------------------


// Fonts
// ==========================================================================

$font-size: 2.2rem !default;
$line-height: round-float(3.0rem/$font-size, 2) !default;
$font-size--mob: 1.6rem !default;
$line-height--mob: round-float(2.0rem/$font-size--mob, 2) !default;
// ----------------------------------------------------
$font-size--small: 2.0rem !default;
$line-height--small: round-float(2.8rem/$font-size--small, 2) !default;
// ----------------------------------------------------
$font-size--smallest: 1.8rem !default;
$line-height--smallest: round-float(2.6rem/$font-size--smallest, 2) !default;
// ----------------------------------------------------
$letter-spacing: normal !default;

// Heading h1
$font-size-h1: 8.0rem !default;
$line-height-h1: round-float(8.0rem/$font-size-h1, 2) !default;
// ----------------------------------------------------
$font-size-h1--md: 4.2rem !default;
$line-height-h1--md: round-float(5.0rem/$font-size-h1--md, 2) !default;
// ----------------------------------------------------
$font-size-h1--sm: 3.0rem !default;
$line-height-h1--sm: round-float(3.2rem/$font-size-h1--sm, 2) !default;
// ----------------------------------------------------
@mixin font-h1() {
	@include font-alt-black;
	font-size: $font-size-h1;
	line-height: $line-height-h1;
	text-align: center;

	//+media('<desk-sm')
	//ont-size: $font-size-h1--md
	//ine-height: $line-height-h1--md

	@include media('<tab-lg') {
		font-size: $font-size-h1--sm;
		line-height: $line-height-h1--sm;
		text-align: left; }

	.home-page & {
		text-align: left; } }

// Heading h2
$font-size-h2: 3.6rem !default;
$line-height-h2: round-float(4.2rem/$font-size-h2, 2) !default;
// ----------------------------------------------------
$font-size-h2--md: 3.6rem !default;
$line-height-h2--md: round-float(4.2rem/$font-size-h2--md, 2) !default;
// ----------------------------------------------------
$font-size-h2--sm: 3.0rem !default;
$line-height-h2--sm: round-float(4.2rem/$font-size-h2--sm, 2) !default;
// ----------------------------------------------------
@mixin font-h2() {
	font-size: $font-size-h2;
	line-height: $line-height-h2;
	text-align: center;

	//+media('<tab-lg')
	//ont-size: $font-size-h2--md
	//ine-height: $line-height-h2--md

	@include media('<tab-lg') {
		font-size: $font-size-h2--sm;
		line-height: $line-height-h2--sm;
		text-align: left; } }

// Heading h3
$font-size-h3: 2.6rem !default;
$line-height-h3: round-float(3.2rem/$font-size-h3, 2) !default;
// ----------------------------------------------------
$font-size-h3--sm: 2.0rem !default;
$line-height-h3--sm: round-float(2.8rem/$font-size-h3, 2) !default;
// ----------------------------------------------------
@mixin font-h3($color: #6bdbbc) {
	@include font-bold;
	font-size: $font-size-h3;
	line-height: $line-height-h3;
	text-align: center;
	color: $color;

	@include media('<tab-lg') {
		font-size: $font-size-h3--sm;
		line-height: $line-height-h3--sm;
		text-align: left; } }

// Heading h4
$font-size-h4: $font-size !default;
$line-height-h4: $line-height !default;
// ----------------------------------------------------
$font-size-h4--sm: $font-size--mob !default;
$line-height-h4--sm: $line-height--mob !default;
// ----------------------------------------------------
@mixin font-h4($color: null) {
	@include font-bold;
	font-size: $font-size-h4;
	line-height: $line-height-h4;
	@if $color {
		color: $color; }

	@include media('<tab-lg') {
		font-size: $font-size-h4--sm;
		line-height: $line-height-h4--sm; } }

// Heading h5
$font-size-h5: $font-size !default;
$line-height-h5: $line-height !default;

// Heading h6
$font-size-h6: $font-size--small !default;
$line-height-h6: $line-height--small !default;

// Small Title Text
@mixin small-title() {
	@include font-normal;
	@include font(1.6rem, round-float(24/16, 2));
	color: rgba($text-color, 0.4); }

// Spaces
$bottom-space: 1.5rem !default;
$bottom-space--mob: 1.1rem !default;
$bottom-space--section: 14rem !default;
$bottom-space--section--mob: 5rem !default;
$bottom-space--article: 7rem !default;
$bottom-space--article--mob: 3rem !default;
$bottom-space--element: 4.5rem !default;
$bottom-space--element--mob: 2rem !default;
$bottom-space--title: 2.4rem !default;
$bottom-space--title--mob: 1.6rem !default;
$bottom-space--row: 1.5rem !default;
$bottom-space--row--mob: 0.9rem !default;


// Z-indexes
// ==========================================================================
$z-high: 40 !default;
$z-middle: 30 !default;
$z-low: 20 !default;
$z-egg: 10 !default;
$z-up: 5 !default;
$z-loader: 1100 !default;
$z-heading: 1200 !default;


// Layout
// ==========================================================================

// отступы для всего сайта
$page-layout-space: 7rem 8rem !default; // v h
$page-layout-space--md: 7rem 4rem !default; // v h
$page-layout-space--mob: 3rem 2rem !default; // v h
// максимальная ширина сайта
$page-layout-width: 192rem !default;
// минимальная ширина сайта
$page-layout-min: 32rem !default;
// минимальные отступы у текствого блока
$text-layout-space: 5rem 6rem !default; // v h
$text-layout-space--md: 2.5rem 3.0rem !default; // v h
$text-layout-space--sm: 1.3rem 1.5rem !default; // v h
$text-layout-space--mob: 0.8rem 1rem !default; // v h
// максимальная ширина текстового блока
$text-layout-width: 68rem !default;
// максимальная ширина блока выходящего за пределы текста
$flood-layout-width: 108rem !default;
// максимальная ширина блока с ограниченной шириной
$wide-layout-width: 124rem !default;
@mixin wide() {
	max-width: $wide-layout-width;
	margin-left: auto;
	margin-right: auto;

	@include media('<tab-lg') {
		max-width: none; } }

$wide-full-layout-width: 140rem !default;
@mixin wide-full() {
	max-width: $wide-full-layout-width;
	margin-left: auto;
	margin-right: auto;

	@include media('<tab-lg') {
		max-width: none; } }

// отступы внутри блочных элементов
$grid-space: 4rem 4rem !default; // v h
$grid-space--md: 3rem 3rem !default; // v h
$grid-space--mob: 2rem 2rem !default; // v h
// отступы в дефолтных списках
$list-space: 34px !default;


// Class of typography styles
// ==========================================================================
// @example Sass - Использование
// .#{$user-content}
//
// @example css - Вывод CSS
// .user-content
$user-content: user-content !default;


// Main Sections
// ==========================================================================
$main-sect-heading-width: 280px;


// Header
// ==========================================================================
$header-width: 520px !default;
$header-width--md: 100% !default;
$header-height: 100vh !default;
$header-height--md: 120px !default;
$header-height--mod: 60px !default;
$header-bg: $bg-color-alt !default;
$nav-is-open-class: 'nav-is-open';

// Sidebar
// ==========================================================================
$sidebar-width: 260px !default;


// Aside
// ==========================================================================
$aside-width: 160px !default;


// Footer
// ==========================================================================
$footer-box-space: 2.5rem 4rem !default; // v h
$footer-box-space--md: 4rem 4rem !default; // v h
$footer-box-space--mob: 2rem 2rem !default; // v h

// Buttons
// ==========================================================================

$btn-height: 60px !default;
$btn-height--mob: 39px !default;
$btn-font-size: 18px !default;
$btn-font-size--mob: 16px !default;
$btn-line-height: 22px !default;
$btn-line-height--mob: 20px !default;
//------------------------------------
$btn-height--small: 44px !default;
$btn-font-size--small: 18px !default;
$btn-line-height--small: 22px !default;
//------------------------------------
$btn-bg-color: $highlight-color !default;
$btn-bg-hover-color: $highlight-color--hover !default;
$btn-text-color: white !default;
$btn-disabled-bg: #ccc !default;
$btn-disabled-color: #969696 !default;


// Forms
// ==========================================================================

// List of inputs
$inputs-list: 'input[type="text"]', 'input[type="password"]', 'input[type="email"]', 'input[type="search"]', 'textarea';
$small-field: 'small-field';

// Colors of forms
$form-text-color: white !default;
$form-placeholder-color: #666695 !default;
$form-bg-color: $base-color-dark !default;
$form-border-color: $form-bg-color !default;
// validation
$form-error-color: $mark-color !default;
$form-success-color: $highlight-color !default;
$form-has-value-color: greenyellow !default;
// disabled
$form-text-color--disabled: rgb(117, 117, 117) !default;
$form-bg-color--disabled: rgb(83, 83, 134) !default;
$form-border-color--disabled: rgb(83, 83, 134) !default;
// focus
$form-border-color--focus: #4949a7 !default;

// Fonts of forms
$form-font-size: 18px !default;
$form-font-size--small: 18px !default;
$form-line-height: 20px !default;
$form-line-height--small: 20px !default;
$form-input-height: 60px !default;
$form-input-height--small: 44px !default;
$form-letter-spacing: null !default;
$form-letter-spacing--small: null !default;

// Font of Labels
$form-label-font-size: 16px !default;
$form-label-font-size--small: 14px !default;
$form-label-line-height: 24px !default;
$form-label-line-height--small: 20px !default;

// Space of Labels
$form-label-vsb: 0.8rem; // vertical space bottom
$form-label-vsb--small: 0.4rem; // vertical space bottom

// @example Sass - Использование
// @example css - Вывод CSS
// Inputs Font
@mixin inputs-font() {
	font-family: inherit;
	font-size: $form-font-size;
	line-height: $form-line-height;
	@if $form-letter-spacing {
		letter-spacing: $form-letter-spacing; }
	color: $form-text-color; }

@mixin inputs-font--small() {
	font-size: $form-font-size--small;
	line-height: $form-line-height--small;
	@if $form-letter-spacing--small {
		letter-spacing: $form-letter-spacing--small; } }

// Selects Font
@mixin selects-font() {
	@include inputs-font; }

@mixin selects-font--small() {
	@include inputs-font--small; }

//+media('<mob-lg')
//ont-size: 10px

// Layout of forms
$form-border-width: 2px !default;
$form-border-width--small: 2px !default;
$form-input-space-vt: floor(($form-input-height - $form-line-height - $form-border-width * 2) / 2) !default; // верхний паддинг округляем в нижнюю сторону
$form-input-space-vt--small: floor(($form-input-height--small - $form-line-height--small - $form-border-width--small * 2) / 2) !default; // верхний паддинг округляем в нижнюю сторону
$form-input-space-vb: ceil(($form-input-height - $form-line-height - $form-border-width * 2) / 2) !default; // нижний паддинг округляем в верхнюю сторону
$form-input-space-vb--small: ceil(($form-input-height--small - $form-line-height--small - $form-border-width--small * 2) / 2) !default; // нижний паддинг округляем в верхнюю сторону
$form-input-space-h: 28px !default;
$form-input-space-h--small: 20px !default;
$form-bottom-space: 2.5rem !default;
$form-bottom-space--small: 2.5rem !default;
$form-bottom-space--mob: 2rem !default;
$form-gap: 4rem !default;

// Vertical spaces
@mixin forms-space() {
	margin: 0 0 $form-bottom-space;

	@include media('<mob-lg') {
		margin: 0 0 $form-bottom-space--mob; } }

@mixin forms-space--small() {
	margin: 0 0 $form-bottom-space--small;

	@include media('<mob-lg') {
		margin: 0 0 $form-bottom-space--mob; } }

// Checkboxes
// Radio Buttons
$checkbox-color: $highlight-color !default;

// Select
$select-arrow-width: 56px !default;
$select-arrow-width--small: 56px !default;
$select-arrow-height: 100% !default;
$select-arrow-height--small: 100% !default;
$multiple-item-space: 3px !default;

// Style Selection
@mixin selectSelection() {
	height: $form-input-height;
	width: 100%;
	padding: $form-input-space-vt $select-arrow-width $form-input-space-vb $form-input-space-h;
	background: $form-bg-color;
	@if $form-border-width {
		border: $form-border-width solid $form-border-color; }
	border-radius: round($form-input-height / 2);
	box-shadow: none;
	text-indent: 0;
	outline: none;
	user-select: none;
	-webkit-tap-highlight-color: transparent; }

@mixin selectSelection--small() {
	height: $form-input-height--small;
	padding: $form-input-space-vt--small $select-arrow-width--small $form-input-space-vb--small $form-input-space-h--small;
	border-radius: round($form-input-height--small / 2);
	@if $form-border-width--small {
		border-width: $form-border-width--small; } }

// Select arrow
@mixin selectArrow() {
	&::before, &::after {
		content: '';
		position: absolute;
		right: 27px;
		top: 50%;
		width: 6px;
		height: 2px;
		background-color: $text-color;
		margin-top: -1px;
		z-index: 1;

		.input-holder.error &, .input-holder.success & {
			display: none; } }

	&::before {
		transform: rotate(45deg); }

	&::after {
		transform: rotate(-45deg);
		right: 23px; } }

// States
@mixin form-success() {
	//color: $form-success-color
	border-color: $form-success-color; }

@mixin form-error() {
	//color: $form-error-color
	border-color: $form-error-color; }

@mixin form-has-value() {
	color: $form-has-value-color;
	border-color: $form-has-value-color; }

@mixin form-on-focus() {
	//color: $form-border-focus-color
	border-color: $form-border-color--focus; }


// Pop
// ==========================================================================
$pop-opener-space: 0.2em 0.7em;
$pop-animation-speed: 0.6s;


// Separator
// ==========================================================================
$sep-size: 5px 46px !default; //h w
$sep-size--mob: 3px 24px !default; //h w
$sep-rotate: -36deg !default;


// Offers Slider
// ==========================================================================
$offers-slider-space: 16px !default;


// Extended Offers Slider
// ==========================================================================
$x-offers-slider-space: 16px !default;


// Rolls default / Roll up list
// ==========================================================================
$rolls-angle-size: 100% 6rem !default; // h w
$rolls-angle-size--md: 100% 4rem !default; // h w
$rolls-angle-size--mob: 4rem 100% !default; // h w


// Detail
// ==========================================================================
$detail-label-width: 20rem !default;
$detail-item-space-v: 3.5rem 1rem !default;
$detail-item-space-v--md: 3.5rem 1rem !default;
$detail-item-space-v--mob: 1.5rem 0.8rem !default;
$detail-box-space-v: 0 2.5rem !default;
$detail-box-space-v--md: 0 2.5rem !default;
$detail-box-space-v--mob: 0 1.2rem !default;
$detail-box-space-h: 1rem 0 !default;


// Animation
// ==========================================================================
$animation-speed: 0.2s !default;
$animation-speed-mouseleave: 0.35s !default;


// Social
// ==========================================================================
$facebook-color: #3a589e !default;
$vkontakte-color: #4b73a4 !default;
$twitter-color: #1da0f2 !default;
$odnoklassniki-color: #f2822e !default;
$youtube-color: #e62117 !default;
$instagram-color-start: #de4f5a !default;
$instagram-color-end: #a43b97 !default;
$viber-color: #574e92 !default;
$telegram-color: #2aa1da !default;
