/* user form */
.user-form {
	margin-bottom: $bottom-space;
	background-color: $base-color-alt;
	border-radius: round($form-input-height/2);
	padding: nth($page-layout-space, 1) nth($page-layout-space, 2);
	@include wide;

	@include media('<desk-lg') {
		padding: nth($page-layout-space--md, 1) nth($page-layout-space--md, 2); }

	@include media('<tab-lg') {
		padding: nth($page-layout-space--mob, 1) nth($page-layout-space--mob, 2); }

	&:last-child {
		margin-bottom: 0; }

	.form-title {
		display: block;
		text-align: center;
		max-width: $text-layout-width;
		@include push-auto;
		margin-bottom: $bottom-space--title;

		@include media('<tab-lg') {
			margin-bottom: $bottom-space--title--mob; }

		// font size
		& {
			@include font-h3; } }

	p {
		max-width: $text-layout-width;
		@include push-auto;

		&:last-child {
