/* user form end */

/* slick sliders */
// slider
.slick-slide {
	-webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
	-webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
	outline: none !important; }

// arrows
.slick-arrow {
	@include size(40px);
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		@include size(12px);
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

// dots
.slick-dots {
	margin: 0;
	line-height: 0;
	pointer-events: none;
	bottom: -2.8rem;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;
		pointer-events: auto;

		&, .#{$user-content} & {
			padding: 3px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 10px;
			height: 10px;
			padding: 0;
			border-radius: 50%;
			background: $text-color-light;
			position: relative;
			transition: background-color $animation-speed ease-in-out;

			&::before {
				content: none; } }

		&.slick-active button {
			background-color: $highlight-color; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* breadcrumbs */
.breadcrumbs {
	margin-bottom: $bottom-space--section;
	@include wide;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--section--mob; }

	&:last-child {
		margin-bottom: 0; } }

.breadcrumbs__list {
	&, .#{$user-content} & {
		padding: 0;
		margin: 0;
		list-style: none;

		li {
			padding: 0 6px 0 0;
			margin: 0;

			&::before, &:first-child::before {
				content: none; } } }

	& {
		cursor: default;
		text-align: center;
		@include font($font-size--small, $line-height--small);

		@include media('<tab-lg') {
			font-size: 12px;
			text-align: left; } }

	.layout-article & {
		max-width: none; }

	li {
		display: inline;
		position: relative;

		&::after {
			display: inline-block;
			vertical-align: baseline;
			content: '/';
			border: none;
			color: $highlight-color;
			padding-left: 10px; }

		&:last-child::after {
			content: none; } }

	a {
		padding-top: 0.5em;
		color: $highlight-color;
		text-decoration: none;
		cursor: pointer;
		// transition
		$prop: color;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		//span
		//order-bottom: 1px solid transparent
		/// transition
		//prop: border-color
		//ransition:
		//property: $prop
		//duration: $animation-speed

		&:hover {
			color: $highlight-color--hover; } } }
//text-decoration: underline

//span
//order-color: $highlight-color

/* breadcrumbs end */

/* social networks */
.social-networks {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.social-networks__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -10px -20px 0; }

.social-networks__item {
	float: left;
	margin: 0 10px 20px;

	a {
		display: block;
		position: relative;
		color: $highlight-color;
		transition: {
			property: color, border-color;
			duration: $animation-speed; }

		span {
			@include hide-text; }

		svg {
			display: block;
			@include size(60px); }

		path {
			fill: currentColor; }

		// hover
		&:hover {
			color: $highlight-color--hover; } } }

/* social networks end */

/* social networks in footer */
.f-soc {
	overflow: hidden;
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; } }

.f-soc__group {
	float: left;
	margin-left: nth($footer-box-space, 2);

	@include media('<desk-lg') {
		margin-left: nth($footer-box-space--md, 2); }

	@include media('<tab-lg') {
		margin-left: nth($footer-box-space--mob, 2); }

	&:first-child {
		margin-left: 0; }

	& > strong {
		display: block;
		margin-bottom: 5px;
		font-size: 14px;
		line-height: 18px;
		color: $text-color-light;

		@include media('<tab-lg') {
			font-size: 12px; } } }

.f-soc__list {
	position: relative;
	display: inline-block;
	vertical-align: top;
	margin: 0 -15px -15px 0;

	@include media('<tab-lg') {
		margin: 0 -10px -10px 0; } }

.f-soc__item {
	float: left;
	margin: 0 15px 15px 0;

	@include media('<tab-lg') {
		margin: 0 10px 10px 0; }

	a {
		display: block;
		position: relative;
		color: $highlight-color;
		transition: {
			property: color, border-color;
			duration: $animation-speed; }

		span {
			@include hide-text; }

		svg {
			display: block;
			@include size(60px);

			@include media('<tab-lg') {
				@include size(40px); } }

		path {
			fill: currentColor; }

		// hover
		&:hover {
			color: $highlight-color--hover; } } }

/* social networks in footer end */

/* table-resp */
.table-resp {
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--section--mob; }

	table, tbody, tr, td, th {
		@include media('<tab-lg') {
			display: block; } }

	thead {
		@include media('<tab-lg') {
			display: none; } }

	tbody {
		@include media('<tab-lg') {
			@include clear-spacing;
			@include margin-horizontal(-#{round($offers-slider-space / 2)}); } }

	table {
		border: none;
		margin: 0;
		width: 100%; }

	tr {
		@include media('<tab-lg') {
			display: inline-block;
			vertical-align: top;
			padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 1) * 0.8, 2);
			background-color: $base-color;
			width: calc(50% - #{$offers-slider-space});
			margin: 0 round(round($offers-slider-space / 2)) $offers-slider-space; }

		@include media('<mob-lg') {
			width: calc(100% - #{$offers-slider-space}); }

		&.is-close {
			td, th {
				opacity: 0.4; } } }

	td, th {
		color: $text-color;
		border: 0 solid rgba(white, 0.2);
		@include font-normal;
		@include font($font-size, round-float(26/22, 1));
		letter-spacing: $letter-spacing;
		text-align: left;
		vertical-align: top;
		@include padding-vertical(0.6rem);
		padding-left: 1rem;

		@include media('<tab-lg') {
			font-size: $font-size--mob; }

		&:first-child {
			padding-left: 0; } }

	td {
		border-bottom-width: 1px;
		@include padding-vertical(1.9rem);

		@include media('<tab-lg') {
			border: none;
			padding: 0.7em 0 0; }

		&::before {
			display: none;
			content: attr(data-label);
			@include font-normal;
			@include font(12px, 14px);
			color: $text-color-light;
			margin-bottom: 4px;

			@include media('<tab-lg') {
				display: block; } }

		&:first-child {
			padding-top: 0; } }

	th, thead td {
		@include small-title; }

	a, a:hover {
		color: inherit;
		text-decoration: none; }

	.map-to {
		.adr {
			margin-bottom: 1px;

			@include media('<tab-lg') {
				margin-bottom: 2px; } }

		.dep {
			font-size: 1.4rem;
			line-height: round-float(26/14, 2);
			color: rgba($text-color, 0.4);

			@include media('<tab-lg') {
				@include font(12px, 15px); } }

		&.active {
			cursor: default;

			.adr {
				color: $highlight-color--hover; }

			.dep {
				color: white; } } }

	ul {
		list-style: none;
		margin: 0;
		padding: 0;

		li {
			padding-left: 0;
			margin-bottom: 0.4rem;

			&:last-child {
				margin-bottom: 0; }

			&::before {
				content: none; } } }

	.pop {
		@include media('<tab-lg') {
			@include margin-vertical(nth($pop-opener-space, 1)); } }

	.pop__content {
		width: 368px; } }


/* table-resp end */

/* schedule */
.schedule {
	&, .#{$user-content} & {
		list-style: none;
		margin: 0;
		padding: 0;

		li {
			padding-left: 0;
			margin-bottom: 0.4rem;

			&::before {
				content: none; } } }

	& {
		li {
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			font-size: 2.0rem;
			line-height: round-float(26/20, 1);
			color: $base-color-dark;

			&.current {
				color: white; } } } }


/* schedule end */

/* section / main sections */
.main-section, .section {
	overflow: hidden;
	@include margin-horizontal(-#{nth($page-layout-space, 2)});
	margin-bottom: $bottom-space--section;
	@include padding-horizontal(#{nth($page-layout-space, 2)});
	@include clear;

	@include media('<desk-lg') {
		@include margin-horizontal(-#{nth($page-layout-space--md, 2)});
		@include padding-horizontal(#{nth($page-layout-space--md, 2)}); }

	@include media('<tab-lg') {
		@include margin-horizontal(-#{nth($page-layout-space--mob, 2)});
		margin-bottom: $bottom-space--section--mob;
		@include padding-horizontal(#{nth($page-layout-space--mob, 2)}); }

	&:last-child {
		margin-bottom: 0; }

	&.clear-space {
		@include padding-horizontal(0); }

	& .clear-space {
		@include margin-horizontal(-#{nth($page-layout-space, 2)});

		@include media('<desk-lg') {
			@include margin-horizontal(-#{nth($page-layout-space--md, 2)}); }

		@include media('<tab-lg') {
			@include margin-horizontal(-#{nth($page-layout-space--mob, 2)}); } } }

.main-section__heading {
	float: left;
	width: $main-sect-heading-width;
	min-height: 50px;
	padding-right: 2rem;

	@include media('<desk-lg') {
		float: none;
		width: auto;
		min-height: 0;
		padding-right: 0;
		margin-bottom: $bottom-space--title; }

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--title--mob; }

	h2, h3, h4,
	.h2, .h3, .h4 {
		margin-bottom: $bottom-space--row;
		text-align: left;

		@include media('<desk-lg') {
			margin-bottom: 0; }

		&:last-child {
			margin-bottom: 0; } } }

.main-section__content {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	margin-left: $main-sect-heading-width;

	@include media('<desk-lg') {
		display: block;
		margin-left: 0; } }

.main-section__caption {
	order: 1;
	@include font(2.2rem, round-float(26/22, 2));
	max-width: 50%;
	margin-bottom: $bottom-space--element;

	@include media('<desk-lg') {
		max-width: none;
		order: unset; }

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob;
		font-size: 1.6rem; } }

.main-section__body {
	width: 100%;
	order: 3;

	@include media('<desk-lg') {
		order: unset;
		margin-bottom: 4.5rem; }

	&:last-child {
		margin-bottom: 0; }

	ul, ol, p {
		margin-top: 0;

		&:last-child {
			margin-bottom: 0; } } }

.main-section__foot {
	order: 2;
	margin-bottom: $bottom-space--element;

	@include media('<desk-lg') {
		order: unset;
		text-align: center;
		margin-bottom: 0; } }

/* section / main sections end */

/* offers */
// slider
.offers {
	margin-bottom: $bottom-space--element;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob; }

	&:last-child {
		margin-bottom: 0; } }

.offers__list {
	overflow: hidden;

	// only for mobile
	@include media('<mob-lg') {
		// before slide load
		white-space: nowrap;
		overflow: visible;
		opacity: 0;

		&.slick-initialized, .no-js & {
			// after slide load
			white-space: normal;
			opacity: 1; }

		.slick-list {
			overflow: visible;
			@include margin-horizontal(-#{round($offers-slider-space / 2)}); } } // компенсировать отступы между слайдами

	// destroy slider for desktop
	@include media('>=mob-lg') {
		.slick-track {
				opacity: 1 !important;
				width: auto !important;
				transform: none !important; }

		.slick-slide {
			width: 100% !important;

			&.slick-cloned {
				display: none !important; } }

		.slick-dots {
			display: none !important; } }

	&, &.slick-dotted {
		margin: 0; }

	//&.slick-dotted
	//include media('<mob-lg')
 }	//margin-bottom: $bottom-space--element

.offers__heads, .offers__item .tr {
	@include clear-spacing; }

.offers__heads, .offers__item {
	.td {
		$item-width: round-float(150 / 960 * 100%, 2);
		display: inline-block;
		vertical-align: top;
		white-space: normal;
		width: $item-width;

		@include media('<mob-lg') {
			width: round-float(100% / 2, 2); }

		&:first-child {
			position: relative;
			width: round-float(100% - ($item-width * 4), 2); } } }

.offers__heads {
	@include padding-vertical(0.7rem, 1.5rem);

	@include media('<mob-lg') {
		display: none; }

	.td {
		@include font(1.6rem, round-float(24/16, 2));
		letter-spacing: $letter-spacing;
		color: rgba($text-color, 0.4); } }

// slide
.offers__item {
	position: relative;
	border-top: 1px solid rgba($text-color, 0.2);
	color: inherit;
	margin-top: -1px;

	// only for mobile
	@include media('<mob-lg') {
		border: none;
		margin: 0;
		@include padding-horizontal(round($offers-slider-space / 2)); // добавить отступы между слайдами
		// before slide load
		float: none;
		display: inline-block;
		vertical-align: top;
		width: 100%;

		.slick-initialized & {
			// after slide load
			float: left;
			display: block; } }

	.td {
		@include font(2.2rem, round-float(30/22, 2));
		letter-spacing: $letter-spacing;
		color: inherit;

		.offers_main & {
			@include font-bold;
			@include font(2.6rem, round-float(26/26, 2)); }

		@include media('<mob-lg') {
			@include font-medium;
			font-size: 2.0rem;
			padding-top: 1rem; }

		&::before {
			display: none;
			content: attr(data-label);
			@include font-normal;
			@include font(13px, 15px);
			color: $text-color-light;
			margin-bottom: 0.4em;

			@include media('<mob-lg') {
				display: block; } }

		&.offers__item__caption {
			@include media('<mob-lg') {
				display: block;
				width: auto;
				background-color: #fff;
				padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 1) * 0.8, 2);
				text-align: center;
				margin: -#{nth($grid-space--mob, 1)} -#{nth($grid-space--mob, 2)} 0.5rem;

				&::before {
					display: none; } } } }

	.tr {
		@include padding-vertical(2rem);

		@include media('<mob-lg') {
			padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 1) * 0.8, 2);
			background-color: $base-color; } } }

.offers__item__img {
	position: relative;
	padding: 0;
	margin: 0 0 nth($page-layout-space--mob, 2);
	display: none;

	@include media('<mob-lg') {
		display: block; }

	&::before {
		content: "";
		display: block;
		@include ratio(210, 90); }

	img {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		max-width: none;
		margin: 0 auto;
		@include object-fit(contain); } }

.offers__item__title {
	@include font-bold;
	margin-bottom: 0.4em;

	@include media('<mob-lg') {
		@include font(2.0rem, round-float(22/20, 2));
		color: #242473;
		margin-bottom: 0.1em; } }

.offers__item__release {
	color: $highlight-color;
	@include font-normal;
	@include font(1.4rem, round-float(18/14, 2));

	@include media('<mob-lg') {
		@include font-medium;
		font-size: 12px; } }

/* offers end */

