 }	//margin-bottom: $bottom-space--element

//.x-offers__heads, .x-offers__item .tr
//include clear-spacing

.x-offers__heads, .x-offers__item {
	table {
		height: 100%;
		width: 100%;
		table-layout: fixed;
		margin: 0; }

	td {
		vertical-align: middle;
		padding: 0 5px 0 0;
