			@include size(30px);

			@include media('<tab-lg') {
				right: nth($grid-space--mob, 2) - 0.6;
				bottom: round-float(nth($grid-space--mob, 2) * 0.8, 2); }

			path {
				fill: currentColor; } } }

	&::before {
		content: "";
		display: block;
		@include ratio(540, 360);
		z-index: -1;

		.news__item_wide & {
			@include ratio(1080, 360); }

		// index page
		.main-section__body & {
			@include ratio(480, 360); }

		.main-section__body .news__item_wide & {
			@include ratio(960, 360); }

		&, .news__item_wide &, .main-section__body &, .main-section__body .news__item_wide & {
			@include media('<mob-lg') {
				//padding: 0
				//height: 203px
				@include ratio(280px, 203px); } } } }

.news__item__figure,
.news__item__body {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	@include size(100%);
	padding: 0;
	margin: 0; }


.news__item__figure {
	display: none;

	.news__item_has-img & {
		display: block;

		&::before {
			$bg-color: #41418b;
			content: "";
			display: block;
			position: absolute;
			left: 0;
			bottom: 0;
			@include size(100%);
			opacity: 0.8;
			@include linear-gradient(to top, rgba($bg-color, 1) 0%, rgba($bg-color, 0) 100%);
			z-index: 1;
			// transition
			will-change: opacity;
			transition: opacity $animation-speed ease-in-out; } }

	img {
		position: absolute;
		left: 0;
		top: 0;
		max-width: none;
		width: 100%;
		height: 100%;
		@include object-fit(cover); } }

.news__item__body {
	z-index: 5;
	padding: nth($grid-space, 1) nth($grid-space, 2) round-float(nth($grid-space, 2) * 0.85, 1);
	display: flex;
	flex-direction: column;
	align-items: flex-start;

	@include media('<tab-lg') {
		padding: nth($grid-space--mob, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 2) * 0.85, 1); }

	.news__item_has-img & {
		justify-content: flex-end; }

	.news__item_wide & {
		text-align: center;
		justify-content: flex-start;
		align-items: center; }

	p {
		&:last-child {
			margin-bottom: 0; } } }

.news__item__title {
	@include font(3.2rem, round-float(38/32, 2));
	overflow: hidden;

	@include media('<tab-lg') {
		@include font(2.0rem, round-float(24/20, 2)); }

	.news__item_has-img & {
		@include font(2.4rem, round-float(29/24, 2));
		color: white;

		@include media('<tab-lg') {
			@include font(1.6rem, round-float(19/16, 2)); } }

	.news__item_wide & {
		@include font(4.8rem, round-float(48/48, 2));
		max-width: 780px;
		@include push-auto;

		@include media('<tab-lg') {
			max-width: 480px;
			@include font(3.0rem, round-float(32/30, 2)); }

		@include media('<mob-lg') {
			@include font(2.0rem, round-float(24/20, 2)); } } }

.btn-source {
	display: inline-block;
	vertical-align: top;
	padding: 4px 16px 5px;
	min-width: 120px;
	@include font-bold;
	@include font(16px, 24px);
	color: $highlight-color;
	background-color: $base-color;
	border-radius: 17px;

	.news__item__body & {
		//margin-bottom: 2.8rem
		margin-bottom: round-float(nth($grid-space, 1) * 0.7, 1);

		@include media('<tab-lg') {
			margin-bottom: round-float(nth($grid-space--mob, 1) * 0.7, 1); } }

	.news__item_wide & {
		//margin-bottom: 4.5rem
		margin-bottom: round-float(nth($grid-space, 1) * 1.125, 1);

		@include media('<tab-lg') {
			margin-bottom: round-float(nth($grid-space--mob, 1) * 1.125, 1); }

		@include media('<mob-lg') {
			margin-bottom: round-float(nth($grid-space--mob, 1) * 0.7, 1); } } }

// hover
.no-touchevents {
	.news__item__inner:hover {
		.news__item__figure {
			&::before {
				opacity: 1; } } }

	.news__item__more {
		.news__item__inner:hover {
			color: white;
			background-color: $highlight-color--hover; } } }

/* news end */

/* pop */
.pop {
	display: inline-block;
	vertical-align: top;
	position: relative;
	color: inherit;
	cursor: default;

	& > span {
		display: inline-block;
		vertical-align: top;
		position: relative;
		padding: 0 nth($pop-opener-space, 2);
		@include font-medium;
		white-space: nowrap;
		cursor: pointer;
		// transition
		transition: color $animation-speed ease-in-out;

		&::before {
			content: "";
			display: block;
			position: absolute;
			left: 0;
			top: -#{nth($pop-opener-space, 1)};
			width: 100%;
			height: calc(100% + #{nth($pop-opener-space, 1) * 2});
			border-radius: 20px;
			border: 2px solid $highlight-color;
			// transition
			$prop: transform, opacity;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $animation-speed;
				timing-function: ease-in-out; } } }

	&:active > span {
		transform: translate(0, 1px); }

	&.pop-is-open > span {
		z-index: 10;

		@include media('>=tab-lg') {
			&::before {
				opacity: 0; } } }

	// hover
	.no-touchevents & > span:hover {
		color: $highlight-color--hover; }

	.no-touchevents &.pop-is-open > span:hover {
		@include media('>=tab-lg') {
			color: $base-color; } } }

.pop__content {
	position: absolute;
	left: 0;
	top: -#{nth($pop-opener-space, 1)};
	width: 440px;
	padding: round-float(nth($grid-space, 2) * 1.25, 1) nth($grid-space, 2) round-float(nth($grid-space, 2) * 0.75, 1);
	color: $base-color;
	background-color: $highlight-color;
	border-radius: 15px;
	z-index: 5;
	opacity: 0;
	pointer-events: none;
	$translate: 0, 2em;
	transform: translate($translate);
	transform: translate3d($translate, 0);
	// transition
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $pop-animation-speed;
		timing-function: cubic-bezier(0.55, 0.085, 0, 0.99); }

	@include media('<tab-lg') {
		position: fixed;
		left: 50%;
		top: 50%;
		padding: round-float(nth($grid-space--mob, 2) * 1.5, 1) nth($grid-space--mob, 2) round-float(nth($grid-space--mob, 2) * 0.75, 1);
		max-width: calc(100% - #{nth($page-layout-space--mob, 2) * 2});
		box-shadow: 0 0 30px rgba(black, 0.2);
		z-index: 15;
		$translate: -50%, calc(-50% + 2em + #{round($header-height--mod / 2)});
		transform: translate($translate);
		transform: translate3d($translate, 0); }

	.pop-is-open & {
		opacity: 1;
		pointer-events: auto;
		$translate: 0, 0;
		transform: translate($translate);
		transform: translate3d($translate, 0);

		@include media('<tab-lg') {
			$translate: -50%, calc(-50% + #{round($header-height--mod / 2)});
			transform: translate($translate);
			transform: translate3d($translate, 0); } } }

.pop__title {
	display: block;
	margin-bottom: 1em; }

.pop__close {
	$line-w: 20px;
	$line-h: 2px;
	position: absolute;
	top: 8px;
	right: 8px;
	width: 20px;
	height: 20px;
	color: white;
	text-indent: -9999px;
	cursor: pointer;
	overflow: hidden;
	opacity: 0;
	$rotate: -0.25turn;
	transform: rotate($rotate);
	transform: rotate($rotate) translateZ(0);
	// transition
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		delay: round-float($pop-animation-speed / 2, 1);
		property: $prop;
		duration: $pop-animation-speed;
		timing-function: ease-in-out; }

	.pop-is-open & {
		opacity: 1;
		$rotate: 0turn;
		transform: rotate($rotate);
		transform: rotate($rotate) translateZ(0);
		transition-delay: 0.2s; }

	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		height: $line-h;
		width: $line-w;
		margin: -#{round($line-h / 2)} 0 0 -#{round($line-w / 2)};
		top: 50%;
		left: 50%;
		background-color: currentColor;
		// transition
		transition: background-color $animation-speed ease-in-out; }

	&::before {
		transform: rotate(0.125turn); }

	&::after {
		transform: rotate(-0.125turn); }

	// hover
	.no-touchevents &:hover {
		&::before, &::after {
			background-color: $base-color; } } }

/* pop end */

/* rolls */
.rolls {
	margin: 0 auto $bottom-space--element;
	max-width: $text-layout-width + nth($page-layout-space, 2) * 2;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--element--mob;
		//max-width: $text-layout-width + nth($page-layout-space--mob, 2) * 2
		max-width: none; }

	&:last-child {
		margin-bottom: 0; } }

// item
.rolls__item {
	position: relative;
	margin-bottom: nth($grid-space, 2);

	@include media('<tab-lg') {
		margin-bottom: nth($grid-space--mob, 2); }

	@include media('<mob-lg') {
		padding-bottom: 4rem; }

	&:last-child {
		margin-bottom: 0; } }

// header
.rolls__header {
	position: relative;
	color: inherit;
	background-color: $base-color-alt;
	padding: 3rem nth($page-layout-space, 2);

	.touchevents & {
		outline: none;
		-webkit-tap-highlight-color: transparent; }

	@include media('<tab-lg') {
		padding: 1.5rem 6rem 1.5rem nth($page-layout-space--mob, 2); }

	@include media('<mob-lg') {
		position: static;
		padding-right: nth($page-layout-space--mob, 2); } }

.rolls__header__inner {
	text-align: left;
	color: inherit;
	margin: 0 auto;
	max-width: $text-layout-width;
	cursor: default; }

// angle
.rolls__angle {
	$burger-size: nth($rolls-angle-size, 1) nth($rolls-angle-size, 2);
	$burger-size--md: nth($rolls-angle-size--md, 1) nth($rolls-angle-size--md, 2);
	$burger-size--mob: nth($rolls-angle-size--mob, 1) nth($rolls-angle-size--mob, 2);
	$layer-size: 2px 20px;
	$layer-space: 4px;
	$layer-color: white;
	$burger-animation-speed: $animation-speed;
	$burger-animation-delay: round-float($burger-animation-speed*0.5, 2);

	$cross-size: nth($layer-size, 1) nth($layer-size, 2);
	$cross-color: $layer-color;

	// =====================

	position: absolute;
	right: 0;
	bottom: 0;
	@include size(nth($burger-size, 1), nth($burger-size, 2));
	background-color: $highlight-color;
	box-shadow: inset 0 0 0 1px $highlight-color--hover;
	cursor: pointer;
	outline: none;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
	transition: background-color $animation-speed ease-in-out;

	@include media('<tab-lg') {
		@include size(nth($burger-size--md, 1), nth($burger-size--md, 2)); }

	@include media('<mob-lg') {
		@include size(nth($burger-size--mob, 1), nth($burger-size--mob, 2)); }

	// крест
	&::before, &::after {
		content: "";
		display: block;
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(nth($cross-size, 1), nth($cross-size, 2));
		margin-top: -#{round(nth($cross-size, 1) / 2)};
		margin-left: -#{round(nth($cross-size, 2) / 2)};
		background-color: $cross-color;
		opacity: 0;
		// transition
		$prop: transform, opacity, background-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $burger-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

	// бутерброд
	& > i {
		//средний слой бутерброда
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(#{nth($layer-size, 1)}, #{nth($layer-size, 2)});
		margin-top: -#{round(nth($layer-size, 1) / 2)};
		margin-left: -#{round(nth($layer-size, 2) / 2)};
		background-color: $layer-color;
		// transition
		$prop: transform, opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $burger-animation-speed;
			delay: $burger-animation-delay; }

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include size(#{nth($layer-size, 1)}, #{nth($layer-size, 2)});
			background-color: $layer-color;
			// transition
			$prop: transform;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $burger-animation-speed;
				delay: $burger-animation-delay; } }

		//верхний слой бутерброда
		&::before {
			top: -#{nth($layer-size, 1) + $layer-space}; }

		//нижний слой бутерброда
		&::after {
			top: #{nth($layer-size, 1) + $layer-space}; } }

	// hover
	.no-touchevents &:hover {
		background-color: $highlight-color--hover; }

	// active
	.is-open & {
		&::before, &::after {
			transition-delay: $burger-animation-delay;
			//background-color: $base-color

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{nth($layer-size, 1) + $layer-space}) translateZ(0); }

			&::after {
				transform: translate(0, -#{nth($layer-size, 1) + $layer-space}) translateZ(0); } } } }

// panel
.rolls__panel {
	display: none; // Важно!
	position: relative;
	padding: nth($page-layout-space, 1) nth($page-layout-space, 2);
	background-color: $base-color-dark;

	@include media('<tab-lg') {
		padding: nth($page-layout-space--mob, 1) 6rem nth($page-layout-space--mob, 1) nth($page-layout-space--mob, 2); }

	@include media('<mob-lg') {
		padding-right: nth($page-layout-space--mob, 2); }

	&::before {
		content: "";
		display: block;
		position: absolute;
		right: 0;
		bottom: 0;
		width: 6rem;
		height: 100%;
		background-color: #488098;

		@include media('<tab-lg') {
			width: 4rem; }

		@include media('<mob-lg') {
			display: none; } }

	// active
	&.is-open {
		display: block; } } // Важно!

.rolls__panel__inner {
	margin: 0 auto;
	max-width: $text-layout-width;
	opacity: 0;
	pointer-events: none;
	will-change: opacity;
	transition: opacity $animation-speed * 2 ease-in-out;

	// active
	.is-open & {
		opacity: 1;
		pointer-events: auto; } }

/* rolls end */
