@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* === LAYOUT === */
html {
	font-size: 62.5%;
	//+scroll-blocked

	// блокировка скролла
	&.css-scroll-fixed:not(.open-only-mob) {
		@include scroll-blocked; }

	&.css-scroll-fixed.open-only-mob {
		@include media('<desk-lg') {
			@include scroll-blocked; } } }

.max-wrap {
	max-width: $page-layout-width; }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto;

	@include media('<desk-lg') {
		@include padding-horizontal(nth($page-layout-space--md, 2)); }

	@include media('<tab-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

.layout-space {
	@include padding-horizontal(nth($page-layout-space, 2));

	@include media('<desk-lg') {
		@include padding-horizontal(nth($page-layout-space--md, 2)); }

	@include media('<tab-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
//.layout-article
.#{$user-content}:not(.home-page) {
	& > p, & > ul, & > ol,
	& > h2, & > h3, & > h4, & > h5, & > h6,
	& > .h2, & > .h3, & > .h4, & > .h5, & > .h6,
	& > hr, & > blockquote,
	& > .layout-text {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto;

		@include media('<tab-lg') {
			max-width: none; } }

	& > h1, & > .h1 {
		max-width: none; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto;

		@include media('<tab-lg') {
			max-width: none; } }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	//.layout-full-width, .layout-wide

	//include margin-horizontal(-#{nth($text-layout-space, 2)})

	//include media('<tab-lg')
	//@include margin-horizontal(-#{nth($text-layout-space--md, 2)})

	//include media('<tab-sm')
 }	//@include margin-horizontal(-#{nth($text-layout-space--sm, 2)})

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	//max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $text-layout-width;

	@include media('<tab-lg') {
		max-width: none; }

	//+media('<tab-sm')
 }	//ax-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal(nth($text-layout-space, 2));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal(nth($text-layout-space--md, 2)); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal(nth($text-layout-space--sm, 2)); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	//max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2}
	max-width: $flood-layout-width;

	//+media('<tab-lg')
	//ax-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}

	//+media('<tab-sm')
 }	//ax-width: #{$flood-layout-width + (nth($text-layout-space--mob, 2))*2}

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal((nth($text-layout-space, 2)));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h2, h3, h4, h5, h6, hr,
	.h2, .h3, .h4, .h5, .h6 {
		max-width: none; } }

// елементы с максимальной ограниченной шириной
.max-wrap-wide {
	max-width: $wide-layout-width; }

.layout-wide {
	max-width: $wide-layout-width;
	@include push-auto; }

// елементы с максимальной ограниченной шириной без отступов
.max-wrap-wide-full {
	max-width: $wide-full-layout-width; }

.layout-wide-full {
	max-width: $wide-full-layout-width;
	@include push-auto; }

// выравнивание по центру блочных элементов
.push-auto {
	margin-left: auto;
	margin-right: auto; }

// layout страницы
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	//min-height: 100%
	height: 100%;
	max-width: 100%;
	width: $page-layout-width; }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	position: relative;
	z-index: $z-middle;
	padding-bottom: $bottom-space--section;

	@include media('<desk-lg') {
		z-index: $z-middle; }

	@include media('<tab-lg') {
		padding-bottom: $bottom-space--section--mob; } }

.main,
.c-heading__align {
	margin-right: $header-width;
	width: calc(100% - #{$header-width});

	@include media('<desk-lg') {
		margin-right: 0;
		width: 100%;
		padding-top: $header-height--md; }

	@include media('<tab-lg') {
		padding-top: $header-height--mod; } }

.content {
	overflow: hidden;
	min-height: 5rem; }

/* === CONTENT end === */

/* === HEADER === */
.header {
	position: fixed;
	left: 0;
	top: 0;
	height: 0;
	width: 100%;
	z-index: $z-low;

	@include media('<desk-lg') {
		z-index: $z-high; } }

.header__layout {
	position: relative;
	margin: 0 auto; }

.header__holder {
	position: absolute;
	right: 0;
	top: 0;
	height: $header-height;
	width: $header-width;
	padding: nth($page-layout-space, 1) 60px;
	z-index: $z-low;
	background-color: $header-bg;
	@include clear;

	@include media('<desk-lg') {
		right: auto;
		left: 0;
		height: 0;
		width: $header-width--md;
		padding: 0;
		z-index: $z-middle; } }

//header top
.header__top {
	position: relative;
	//margin-bottom: 10.6rem
	margin-bottom: 7rem;

	@include media('<desk-lg') {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: $header-height--md;
		margin: 0;
		padding: 1rem nth($page-layout-space--md, 2);
		background-color: $header-bg;
		z-index: $z-middle; }

	@include media('<tab-lg') {
		padding: 1rem nth($page-layout-space--mob, 2);
		height: $header-height--mod; }

	&:last-child {
		margin-bottom: 0; } }

//logo
.logo {
	display: block;
	position: relative;
	background-image: url(../../img/logo.png);
	//background-repeat: no-repeat
	//background-size: contain
	width: 341px;
	//height: 36px
	text-indent: -9999px;
	overflow: hidden;
	z-index: 5;

	@include media('<tab-lg') {
		background-image: url(../../img/logo-sm.png);
		width: 200px;
		height: 33px; }

	&::before {
		content: "";
		display: block;
		@include ratio(341, 36);

		@include media('<tab-lg') {
			@include ratio(200, 33); } }

	a, & > span {
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		@include size(100%); } }

//header-menu
.header-menu {
	display: none;
	$hs: 6rem;
	list-style: none;
	padding: 0;
	//margin: 0 -#{round($hs / 2)}
	margin: 0;
	@include font-bold;
	//+font(3.6rem, $header-height--md)
	@include font(3.6rem, round-float(40/36, 2));
	@include clear;

	@include media('<desk-lg') {
		display: block;
		padding-right: $header-height--md; }

	@include media('<tab-lg') {
		display: none; }

	li {
		float: left;
		//+padding-horizontal(round($hs / 2))
		padding-left: $hs;

		&:first-child {
			padding-left: 0; } }

	a {
		color: $base-color;
		transition: color $animation-speed ease-in-out;

		.no-touchevents &:not([href*="javascript:;"]):hover {
			color: $highlight-color; } }

	// current
	li.current {
		a {
			color: $highlight-color; } } }

//egg
.egg {
	bottom: -63px;
	right: 9px;
	position: fixed;
	overflow: hidden;
	width: 590px;
	font-size: 0;
	line-height: 0;
	background: url(../../img/egg.png) no-repeat;
	background-size: contain;
	z-index: $z-high - 5;
	pointer-events: none;

	@include media('<desk-lg') {
		display: none; }

	&::before {
		content: "";
		position: relative;
		display: block;
		@include ratio(590px, 450px); }

	.header & {
		display: none;
		position: absolute;
		bottom: auto;
		top: 380px;
		right: auto;
		left: -139px;
		opacity: 1;
		z-index: $z-egg;
		// transition
		$prop: transform, opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed * 2;
			timing-function: ease-in-out; }

		@include media('<desk-lg') {
			display: block;
			left: auto;
			right: -190px;
			top: auto;
			bottom: -36px;
			width: 470px;
			opacity: 0;
			$scale: 0.8, 0.8;
			transform: scale($scale);
			transform: scale3d($scale, 1); }

		@include media('<mob-lg') {
			left: auto;
			right: -67px;
			top: auto;
			bottom: -6px;
			width: 194px; } }

	.#{$nav-is-open-class} &, // add js
	.no-js & {
		@include media('<desk-lg') {
			opacity: 1;
			$translate: 0, 0;
			transform: translate($translate); // for old browser
			transform: translate3d($translate, 0); } } }

/* === HEADER end === */

/* === NAVIGATION === */
.nav-wrap {
	position: relative;
	background-color: $header-bg;
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		property: transform, opacity;
		duration: $animation-speed; }

	@include media('<desk-lg') {
		overflow: hidden;
		position: absolute;
		left: 0;
		top: $header-height--md;
		width: 100%;
		padding: $page-layout-space--md;
		z-index: $z-middle - 1;

		$translate: 0, -100%;
		//$translate: 0, 0
		transform: translate($translate); // for old browser
		transform: translate3d($translate, 0); }

	@include media('<tab-lg') {
		padding: 3.3rem nth($page-layout-space--mob, 2) 9.3rem;
		top: $header-height--mod; }

	&.#{$nav-is-open-class}, // add js
	.no-js & {
		@include media('<desk-lg') {
			$translate: 0, 0;
			transform: translate($translate); // for old browser
			transform: translate3d($translate, 0); } }

	.max-wrap-text {
		@include media('<tab-lg') {
			max-width: none; } } }

.nav {
	position: relative;

	@include media('<desk-lg') {
		opacity: 0;
		//opacity: 1
		// transition
		$prop: transform, opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed * 2;
			timing-function: ease-in-out; } }

	.#{$nav-is-open-class} & {
		@include media('<desk-lg') {
			opacity: 1;
			transition-delay: $animation-speed / 2; } } }

.nav__list {
	position: relative;
	list-style: none;
	//margin: 0 0 4.2rem
	margin: 0 0 3rem;
	padding: 0;
	@include font($font-size--small, $line-height--small);

	@include media('<tab-lg') {
		@include font($font-size--mob, $line-height--mob); }

	li {
		//margin-bottom: 1.3rem
		margin-bottom: 0.5rem;

		@include media('<tab-lg') {
			margin-bottom: 0.9rem; }

		&.high {
			@include font-bold;
			@include font(3.6rem, round-float(49/36, 2));
			margin-bottom: 0;

			@include media('<tab-lg') {
				@include font(2.6rem, round-float(34/26, 2)); } }

		&:last-child {
			margin-bottom: 0; } }

	a {
		color: $base-color;
		text-decoration: none;
		// transition
		transition: color $animation-speed ease-in-out; }

	li.current a,
	a:not([href*="javascript:;"]):hover {
		text-decoration: none;
		color: $highlight-color; } }

// nav opener btn
a.nav-opener {
	$burger-bg: transparent;
	$burger-size: $header-height--md;
	$burger-size--mob: $header-height--mod;
	$layer-width: 43px;
	$layer-width--mob: 25px;
	$layer-height: 7px;
	$layer-height--mob: 4px;
	$layer-space: 7px;
	$layer-space--mob: 4px;
	$layer-color: $highlight-color;
	$burger-animation-speed: $animation-speed;
	$burger-animation-delay: round-float($animation-speed*0.5, 2);

	$cross-width: $layer-width;
	$cross-width--mob: $layer-width--mob;
	$cross-height: $layer-height;
	$cross-height--mob: $layer-height--mob;
	$cross-color: $layer-color;

	// =====================

	display: none;
	position: absolute;
	right: nth($page-layout-space--md, 2);
	top: round($header-height--md / 2);
	margin-top: -#{round($burger-size/2)};
	@include size($burger-size);
	z-index: $z-middle;
	text-decoration: none;
	@include font-normal;
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;
	color: $text-color;
	background-color: $burger-bg;
	user-select: none;
	-webkit-tap-highlight-color: transparent;

	@include media('<desk-lg') {
		display: block; }

	@include media('<tab-lg') {
		right: nth($page-layout-space--mob, 2);
		top: round($header-height--mod / 2);
		margin-top: -#{round($burger-size--mob/2)};
		@include size($burger-size--mob);
		@include font($font-size--mob, $line-height--mob); }

	& > span {
		position: absolute;
		left: 100%;
		top: 0;
		height: 100%;
		@include padding-horizontal(5px);
		@include font-bold;
		line-height: $header-height--md;
		display: none; }

	// крест
	&::before, &::after {
		@include pseudo();
		@include position(50%);
		@include size($cross-height, $cross-width);
		margin-top: -#{round($cross-height / 2)};
		margin-left: -#{round($cross-width / 2)};
		background-color: $cross-color;
		opacity: 0;
		$prop: transform, opacity, background-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $burger-animation-speed;
			delay: 0s;
			timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

		@include media('<tab-lg') {
			@include size($cross-height--mob, $cross-width--mob);
			margin-top: -#{round($cross-height--mob / 2)};
			margin-left: -#{round($cross-width--mob / 2)}; } }

	// бутерброд
	& > i {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size($layer-height, $layer-width + 10);
		margin-top: -#{round($layer-height / 2)};
		margin-left: -#{round($layer-width / 2)};
		background-color: $layer-color;
		$prop: transform, opacity, background-color, max-width;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $burger-animation-speed;
			delay: $burger-animation-delay; }

		@include media('<tab-lg') {
			@include size($layer-height--mob, $layer-width--mob + 5);
			margin-top: -#{round($layer-height--mob / 2)};
			margin-left: -#{round($layer-width--mob / 2)}; }

		&::before, &::after {
			@include pseudo();
			left: 0;
			@include size($layer-height, $layer-width);
			background-color: inherit;
			$prop: transform, max-width;
			will-change: $prop;
			transition: {
				property: $prop;
				duration: $burger-animation-speed;
				delay: $burger-animation-delay; }

			@include media('<tab-lg') {
				@include size($layer-height--mob, $layer-width--mob); } }

		//first line menu
		&::before {
			top: -#{$layer-height + $layer-space};
			max-width: $layer-width - 5;

			@include media('<tab-lg') {
				top: -#{$layer-height--mob + $layer-space--mob};
				max-width: $layer-width--mob - 5; } }

		//third line menu
		&::after {
			top: #{$layer-height + $layer-space};

			@include media('<tab-lg') {
				top: #{$layer-height--mob + $layer-space--mob}; } } }

	// hover
	.no-touchevents &:hover {
		//background-color: #f3f3f3

		&::before, &::after {
			background-color: $highlight-color--hover; }

		& > i {
			background-color: $highlight-color--hover;
			//max-width: $layer-width

			&::before, &::after {
				background-color: inherit; }

			//&::before
 } }			//ax-width: $layer-width

	// active
	&.nav-is-open {

		&::before, &::after {
			transition-delay: $burger-animation-delay;
			//background-color: $base-color

			opacity: 1; }

		&::before {
			transform: rotate(45deg) translateZ(0); }

		&::after {
			transform: rotate(-45deg) translateZ(0); }

		& > i {
			opacity: 0;
			transition-delay: 0s;

			&::before, &::after {
				transition-delay: 0s; }

			&::before {
				transform: translate(0, #{$layer-height + $layer-space}) translateZ(0);

				@include media('<tab-lg') {
					transform: translate(0, #{$layer-height--mob + $layer-space--mob}) translateZ(0); } }

			&::after {
				transform: translate(0, -#{$layer-height + $layer-space}) translateZ(0);

				@include media('<tab-lg') {
					transform: translate(0, -#{$layer-height--mob + $layer-space--mob}) translateZ(0); } } } } }

// overlay
// в html добавить
// <div class="nav-overlay nav-overlay-js">&nbsp;</div>
.overlay {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(black, 0.35);
	z-index: 9998;
	box-sizing: border-box;
	opacity: 0;
	visibility: hidden;
	$prop: opacity, visibility;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	&.nav-overlay {
		top: 0;
		height: 100%; }

	&.open-only-mob {
		@include media('>=desk-lg') {
			display: none; } }

	&.#{$nav-is-open-class} {
		opacity: 1;
		visibility: visible; }

	&.filters-is-open, &.request-is-open {
		opacity: 1;
		visibility: visible; } }

/* navigation end */

/* === NAVIGATION end === */

/* === FOOTER === */
.footer {
	position: relative;
	width: 100%;
	padding-top: nth($page-layout-space, 1);
	padding-bottom: nth($page-layout-space, 1) + 3rem;
	z-index: $z-high;
	background-color: $base-color;
	border-top: 5px solid #363c9b;

	@include media('<desk-lg') {
		@include padding-vertical(nth($footer-box-space--md, 1));
		z-index: $z-low; }

	@include media('<tab-lg') {
		@include padding-vertical(nth($footer-box-space--mob, 1), round-float(nth($footer-box-space--mob, 1) * 2, 1)); }

	&::before {
		content: "";
		display: block;
		position: absolute;
		left: 50%;
		width: 50%;
		height: 100%;
		top: 0;
		z-index: -1;
		background-color: $base-color-alt;

		@include media('<desk-lg') {
			display: none; } } }

.footer__holder {
	margin: -#{nth($footer-box-space, 1)} -#{nth($footer-box-space, 2)};
	@include clear;

	@include media('<desk-lg') {
		display: flex;
		flex-wrap: wrap;
		align-items: stretch;
		margin: -#{nth($footer-box-space--md, 1)} -#{nth($footer-box-space--md, 2)}; }

	@include media('<tab-lg') {
		margin: -#{nth($footer-box-space--mob, 1)} -#{nth($footer-box-space--mob, 2)}; } }

//.f-boxes
//idth: 50%

.f-col {
	width: calc(100% / 4);
	min-height: 1px;
	@include clear;

	@include media('<desk-lg') {
		width: calc(100% / 3);

		&::after {
			content: none; } }

	@include media('<desk-sm') {
		width: calc(100% / 1); } }

.f-box {
	min-height: 1px;
	padding: $footer-box-space;

	@include media('<desk-lg') {
		padding: $footer-box-space--md; }

	@include media('<tab-lg') {
		padding: $footer-box-space--mob; }

	.f-box__title {
		@include font-bold;
		@include font(2.6rem, round-float(30/26, 2));
		margin-bottom: 1.5rem;

		@include media('<tab-lg') {
			font-size: 2.0rem; } } }

// footer box appeal form
.f-box_form {
	background-color: $base-color-alt;

	@include media('<desk-lg') {
		position: relative; } }

// footer box developer
.developer {
	position: absolute;
	right: 0;
	bottom: 0;
	padding: 0 nth($page-layout-space, 2) nth($page-layout-space, 1) 0;
	width: auto;
	line-height: 0;
	text-align: right;

	@include media('<desk-lg') {
		position: relative;
		right: auto;
		bottom: auto;
		padding: 0; }

	@include media('<mob-lg') {
		display: block;
		text-align: center; }

	a {
		display: inline-block;
		vertical-align: top;
		color: $highlight-color;
		text-decoration: none;
		// transition
		transition: color $animation-speed ease-in-out;

		&:hover {
			color: $highlight-color--hover;
			text-decoration: none; }

		svg {
			display: inline-block;
			vertical-align: top;
			@include size(15px, 70px);
			margin-right: 5px;

			@include media('<mob-lg') {
				margin: 0 0 5px; }

			path {
				fill: currentColor; } }

		& > span {
			display: inline-block;
			vertical-align: top;
			font-size: 14px;
			line-height: 15px;

			@include media('<tab-lg') {
				font-size: 12px; }

			@include media('<mob-lg') {
				display: block;
				font-size: 14px; } } } }

.req {
	@include media('<desk-lg') {
		margin-bottom: nth($footer-box-space--md, 1); }

	@include media('<tab-lg') {
		margin-bottom: nth($footer-box-space--md, 1); }

	&:last-child {
		margin-bottom: 0; } }

// footer order
.f-box {
	width: calc(100% / 4);

	@include media('<desk-lg') {
		width: calc(100% / 3); }

	@include media('<desk-sm') {
		width: calc(100% / 2); }

	.f-col & {
		width: 100%;

		@include media('<desk-sm') {
			width: calc(100% / 2); }

		@include media('<tab-sm') {
			width: calc(100% / 1); } } }

.f-col_nav {
	//order: 1
	float: left;

	@include media('<desk-lg') {
		float: none;
		display: flex;
		flex-direction: column;
		justify-content: space-between; }

	@include media('<desk-sm') {
		flex-direction: row;
		flex-wrap: wrap; }

	.f-box_subs {
		order: 2; }

	.f-box_nav {
		order: 1; }

	.f-box_soc {
		order: 3; } }

.f-col_cont {
	//order: 2
	float: right;
	width: calc(100% / 4 * 3);

	@include media('<desk-lg') {
		float: none;
		display: flex;
		flex-wrap: wrap;
		width: calc(100% / 3 * 2); }

	@include media('<desk-sm') {
		width: calc(100% / 2 * 2); }

	.f-box_cont, .f-box_req {
		float: left;
		width: calc(100% / 3 * 1);
		clear: left;

		@include media('<desk-lg') {
			float: none;
			width: calc(100% / 2 * 1); }

		@include media('<mob-lg') {
			width: 100%; } }

	.f-box_form {
		float: right;
		width: calc(100% / 3 * 2);

		@include media('<desk-lg') {
			float: none;
			width: 100%; } } }

