// heading
.heading {
	padding: nth($page-layout-space, 1) 0 0;
	margin-bottom: $bottom-space--article;
	text-align: center;
	@include wide;

	@include media('<desk-lg') {
		padding: nth($page-layout-space--md, 1) 0 0; }

	@include media('<tab-lg') {
		padding: nth($page-layout-space--mob, 1) 0 0;
		//margin-bottom: $bottom-space--article--mob
		margin-bottom: 1rem;
		text-align: left; } }

.c-heading {
	$tf: cubic-bezier(0.4, 0.6, 0.2, 1);
	opacity: 0;
	transition: {
		property: opacity, animation;
		duration: 0.8s;
		timing-function: $tf; }

	animation: {
		duration: 1s;
		timing-function: $tf; }

	&.is-active {
		opacity: 1;
		transition: {
			delay: 0.2s; }
		animation: {
			fill-mode: forwards;
			name: showHeading; } }

	&.is-leaving {
		opacity: 0;
		animation: {
			fill-mode: forwards;
			duration: 0s;
			delay: 1s;
			name: hideHeading; }

		transition: {
			timing-function: ease-in-out; } } }

$c-heading-y: 60vh;
@keyframes showHeading {
	0% {
		transform: translate(0, $c-heading-y); }

	30% {
		transform: translate(0, $c-heading-y); }

	100% {
		transform: translate(0, 0); } }

@keyframes hideHeading {
	0% {
		transform: translate(0, 0); }

	100% {
		transform: translate(0, $c-heading-y); } }

