// c-transition
.c-transition {
	//background-color: #2a2971
	background-color: $base-color;
	cursor: wait;
	height: 100vh;
	left: 0;

	overflow: hidden;
	pointer-events: none;
	position: fixed;
	top: 100vh;
	transform: translate3d(0, 100%, 0);
	width: 100%;
	z-index: $z-loader; }

.c-transition .u-h1 {
	left: 50%;
	position: absolute;
	text-align: center;
	top: 50%;
	transform: translate(-50%, 100vh);
	transition: transform 1s cubic-bezier(0.4, 0.6, 0.2, 1);
	transition-delay: 0.2s; }

.c-transition.is-active {
	animation-delay: 0s;
	animation-duration: 1s;
	animation-fill-mode: forwards;
	animation-name: fade-in;
	animation-timing-function: cubic-bezier(0.55, 0.085, 0, 0.99);
	top: 0; }

.c-transition.is-active .u-h1 {
	transform: translate(-50%, -50%); }

.c-transition.is-leaving {
	animation-duration: 1s;
	animation-name: fade-out; }

.c-transition.is-leaving .u-h1 {
	transform: translate(-50%, -100vh);
	transition-delay: 0s; }

@keyframes fade-out {
	0% {
		transform: translateZ(0); }

	to {
		transform: translate3d(0, -100%, 0); } }

@keyframes fade-in {
	0% {
		transform: translate3d(0, 100%, 0); }

	to {
		transform: translateZ(0); } }

